export const dynamic = 'force-dynamic';

interface BuildingDetailPageProps {
  params: { id: string };
}

export async function generateMetadata({ params }: BuildingDetailPageProps) {
  const building = await getBuildingById(params.id);
  if (!building) {
    return {
      title: '作品未找到',
    };
  }
  return {
    title: `${building.name} - 像素建筑馆`,
    description: `参与第 ${building.eventEdition} 届建筑节活动。${building.description}`,
  };
}

