function TerritoryCommandsCard({ building }: { building: Building }) {
  const eventYearSuffix = building.eventYear.toString().slice(-2);
  const territoryEventPrefix = `[${eventYearSuffix}year-builder]`;
  const fullTerritoryName = `${territoryEventPrefix}${building.territoryIdentifier}`;
  
  const territoryTeleportCommand = `/res tp ${fullTerritoryName}`;

  return (
    <Card className="mt-6 shadow-md">
      <CardHeader>
        <CardTitle className="text-xl flex items-center">
          <Landmark className="mr-2 h-5 w-5 text-primary" />
          领地信息与指令
        </CardTitle>
        <CardDescription>游戏内领地相关操作指令与信息。</CardDescription>
      </CardHeader>
      <CardContent className="space-y-4">
        <div>
          <h4 className="font-semibold text-foreground/90 mb-1">领地传送指令 (按名称):</h4>
          <div className="flex items-center justify-between p-3 bg-muted rounded-md">
            <code className="text-sm break-all font-mono">{territoryTeleportCommand}</code>
            <CopyCommandButton commandToCopy={territoryTeleportCommand} buttonText="复制传送" />
          </div>
           <p className="text-xs text-muted-foreground mt-1">用于直接传送到指定名称的领地。</p>
        </div>
        
        <Separator />
        
        <div>
          <h4 className="font-semibold text-foreground/90 mb-2">领地详细信息:</h4>
          <div className="space-y-1 text-sm text-muted-foreground mb-2">
            <p className="flex items-center"><ServerIcon className="mr-2 h-4 w-4 text-primary/80" />服务器: <span className="font-medium text-foreground/80 ml-1">{building.server}</span></p>
            <p className="flex items-center"><Waypoints className="mr-2 h-4 w-4 text-primary/80" />坐标 (X, Y, Z): <span className="font-medium text-foreground/80 ml-1">{building.coordinates.x}, {building.coordinates.y}, {building.coordinates.z}</span></p>
            <p className="flex items-center"><Terminal className="mr-2 h-4 w-4 text-primary/80" />完整领地名: <span className="font-medium text-foreground/80 ml-1">{fullTerritoryName}</span></p>
             <p className="flex items-center"><CalendarDays className="mr-2 h-4 w-4 text-primary/80" />活动届数: <span className="font-medium text-foreground/80 ml-1">第 {building.eventEdition} 届 ({building.eventYear}年)</span></p>
          </div>
        </div>
      </CardContent>
    </Card>
  );
}


export default async function BuildingDetailPage({ params }: BuildingDetailPageProps) {
  const buildingId = params.id;
  const [building, currentUser] = await Promise.all([
    getBuildingById(buildingId),
    getUserSession()
  ]);

  if (!building) {
    notFound();
  }

  return (
    <div className="space-y-8">
      <Link href="/" className="inline-flex items-center text-sm text-primary hover:underline mb-4">
        <ArrowLeft className="mr-1 h-4 w-4" />
        返回首页
      </Link>

      <Card className="overflow-hidden shadow-xl">
        <div className="grid md:grid-cols-2 gap-0">
          <div className="relative aspect-[4/3] md:aspect-auto">
            <Image
              src={building.imageUrl}
              alt={building.name}
              layout="fill"
              objectFit="cover"
              priority
              data-ai-hint={building.aiHint || "minecraft architecture"}
              className="md:rounded-l-lg"
            />
          </div>
          <div className="flex flex-col">
            <CardHeader className="pb-4">
              <CardTitle className="text-3xl lg:text-4xl font-bold text-primary">{building.name}</CardTitle>
              <p className="text-sm text-muted-foreground mt-2 flex items-center">
                <CalendarDays className="mr-1.5 h-4 w-4" />
                参与第 {building.eventEdition} 届建筑节活动 ({building.eventYear}年)
              </p>
              <div className="flex flex-wrap gap-4 items-center mt-2">
                <CardDescription className="flex items-center text-muted-foreground text-base">
                  <UserIcon className="mr-1.5 h-5 w-5" /> 作者: {building.author}
                </CardDescription>
                <Badge variant="default" className="text-base px-3 py-1 flex items-center">
                  <Scaling className="mr-2 h-4 w-4" />
                  规格: {building.size}
                </Badge>
              </div>
            </CardHeader>
            <CardContent className="flex-grow pt-0">
              <Separator className="my-4" />
              <p className="text-foreground/90 leading-relaxed text-base">{building.description}</p>
            </CardContent>
            <CardFooter className="flex flex-col sm:flex-row justify-between items-center gap-4 pt-6 border-t bg-muted/30 md:rounded-br-lg p-6">
              <div className="flex items-center text-accent text-xl">
                <ThumbsUp className="mr-2 h-6 w-6" />
                <span className="font-semibold">{building.votes}</span>
                <span className="ml-1.5 text-base">票</span>
              </div>
              <VoteButton buildingId={building.id} currentUser={currentUser} initialVotes={building.votes} />
            </CardFooter>
          </div>
        </div>
      </Card>
      
      <TerritoryCommandsCard building={building} />
      <SizeSpecificationTable />

    </div>
  );
}
